setScale(scale) {
  const svgs = this.shadowRoot!.querySelectorAll('svg');
  svgs.forEach(svg => {
    svg.style.transform = `scale(${scale})`;
    svg.style.transformOrigin = 'center'; // Asegúrate de que escalen desde el centro
  });
}
setGap(gapValue) {
  const container = this.shadowRoot!.querySelector('.hands-container') as HTMLElement;
  if (container) {
    container.style.gap = gapValue;
  }
}

